.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}




.navMenuApps {
  margin: 0.7rem;
  border: 1px solid blue;
  padding: 0.5rem;
  background: transparent;
  border-radius: 0.5rem;
  cursor: pointer
}

.navMenuApps a {
  color: blue;


}

.seleccionado {
  background: blue;
  color: white;
}

.seleccionado a {
  /*a en realidad es Link*/
  color: white;

}

.divAppMain {
  margin: 2rem
}



 
.app-aContenedorLinkPaginas {
  height: 100%;
  width: 100%;
  padding: 0;
  align-content: center;
  
}
 
 

 








