/* Estilos para el fondo oscuro que cubre el contenido cuando se muestra la ventana modal */
.sp-modal-background {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9);
    z-index: 9999999999
}

/* Estilos para el contenedor de la ventana modal */
.sp-modal {
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    z-index: 9999999999
}

.sp-info-img {
    cursor: pointer;
    width: 40px;
    height: 40px;
    border-radius: 20px;
    /*tiene que ser la mitad del width/height*/

    border: 1px solid #666;
    background-size: 50%;
    margin-bottom: 0.7rem;
}


.sp-rotateImg {
    animation: sp-spin 5s infinite linear;
}

@keyframes sp-spin {
    0% {
        transform: rotateY(0);
    }

    100% {
        transform: rotateY(1turn);
    }

}

.sp-rotateImg:hover {
    animation: none;
    transform: scale(1.1);
    transition: transform 0.5s ease-in-out;
}



