/*Responsive design*/
/* @media (max-width: 991px) { cambio de 10/5/24, para que siempre se muestre responsive tambien en pc*/
@media (max-width: 1500px) {
    /*ES responsive*/

    .sp-ms-optionsNoMenuRes {
        /*no menu responsive*/
        display: none;
    }

    /* .navbar {
        position: relative;
        z-index: 99999999;
      } */

    .sp-ps-articlePost {
        margin: 0.3rem 0.1rem;
        padding: 1rem;
    }

    .sp-ms-DivDivTextAreaWrite {
        margin: 0.1rem;
        margin-top: 0.3rem;
    }

    .sp-ps-toast {
        position: fixed;
        bottom: 20px;
        /* Puedes ajustar la posición vertical según tus necesidades */
        left: 50%;
        transform: translateX(-50%);
        z-index: 9999;
        /* Asegura que el toast esté por encima de todo */
        background-color: #333;
        color: #fff;
        padding: 10px 20px;
        border-radius: 4px;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
        /*left: 50%;: Esta propiedad establece la posición horizontal del elemento. Al establecerlo en 50%, se coloca el elemento justo en el centro horizontal de la ventana del navegador.
        transform: translateX(-50%);: Esta propiedad de transformación se utiliza junto con left: 50% para ajustar la posición horizontal del elemento. Al establecerlo en -50%, el elemento se desplaza hacia la izquierda en un 50% de su propio ancho, lo que lo coloca perfectamente centrado horizontalmente.*/
    }

    .sp-ps-toast.show {
        opacity: 1;
        transition: opacity 0.3s ease-in-out;
    }

    .sp-ps-toast.hide {
        opacity: 0;
        transition: opacity 0.3s ease-in-out;
    }


