/*GENERALES*/
/* Define la animación de salida */
@keyframes colorinche-quit_quitar {

    /*no se usa 5/12/23*/
    0% {

        color: transparent;
        background: red;
    }

    50% {
        background: #0000FF80;
        color: transparent
    }
}

.cron_NotCursor {
    cursor: not-allowed;
}

.cron-btn:hover {
    transform: scale(1.01);
    transition: all .1s ease-in-out;
    box-shadow: 0px 0px 10px #6e2f8a;
}

.cron_fwBold {
    font-weight: bold;
}

.cron-iconoColor {
    color: black;
    font-size: 1.3rem
}


.cron_clBlack {
    color: black
}







/*INDEX*/
.cron-index-resaltarCard {
    /*colorinche*/
    /* background: greenyellow !important; */
    background: radial-gradient(#e66465, #9198e5) !important;
    /* transform: scale(1.02);  26/12/23        comentado. causa problemas con la ventana modal      */
    transition: background 0.1s, transform 0.1s;

    box-shadow: 0px 0px 6px 6px #9198e580;
    /*agregado por problema con transform*/


}

/* Define una nueva clase para la transición de salida. que vuelva a su estado original */
.cron-index-resaltarCard_quit {
    /*colorinche-quit*/
    /* background-color: blue; */


    /* transform: scale(1);  26/12/23        comentado. causa problemas con la ventana modal */
    transition: background 0.3s, transform 0.3s;


    /*animation: colorinche-quit_quitar 0.4s linear; 30/11/23*/

    box-shadow: none;

}

.cron-index-containerSeccion {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    /* height: 80vh; */
    background-color: #8e44ad;
    color: #fff;
    text-align: center;
    /* padding: 50px; */

    border: 2px solid #835696;
    border-radius: 25px;
}

.cron-index-divArrastrable {
    /* background: red; */
    border: 1px solid #8e44ad;
    border-radius: 25px;
    width: 50%;
    margin: auto;
    cursor: pointer;
    padding: 20px;
    background-color: #c8a8d6
}

.cron-index-divArrastrable:hover {
    box-shadow: 0px 0px 10px #6e2f8a;
}

.cron-index-btnNuevaSeccion {
    margin: 1rem
}

.cron-index-divNoSeccion {
    font-size: 1.5rem;
    width: 20rem;
    border-radius: 25px;
    color: white;
    background: rgba(0, 0, 0, 0.712);
    margin: 2rem;
    padding: 1rem;
}









/*SECCION*/
.cron-secc-divSeccion {
    color: white;
    border-radius: 25px;
    margin: 2rem;
    max-width: 25rem;
    position: relative;
    font-weight: bold;
    min-height: 10rem
}

.cron-secc-divSeccion:hover {
    box-shadow: 0px 0px 10px black;
}

.cron-secc-btnColor {
    padding: 0.3rem;
    border-radius: 50%;
    border: none;
    position: absolute;
    left: .5rem;
    top: .5rem
}

.cron-secc-inputNombreSeccion {
    margin-top: 2rem;
    background: transparent;
    font-weight: bold;
    border: none;
    border-bottom: 1px solid black
}

.cron-secc-btnDelete {
    background: none;
    color: white;
    position: absolute;
    right: 0;
    top: 0;
    border: none;
    font-size: 1.5rem
}

