/*modal ventana flotante edicion minutos manual*/
.cron-secc-modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.7);

}

.cron-secc-modal-content {
    background-color: #fefefe;
    margin: 10% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
}

.cron-secc-close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.cron-secc-close:hover,
.cron-secc-close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}