* {
    text-align: center;
}

button {
    margin: 0;
    /*es para normalizar agregado 4/4/23*/
}

.dy-bl {
    display: block;
}

.similTabla {
    margin-top: 3rem;
    display: flex;
    width: 100%;

    text-align: center;
    /* grid-template-columns:   repeat(2, 1fr);; */

    justify-content: space-around
}


.labelEditable {
    font-weight: bold;
    font-size: 1.5rem;
    cursor: text;
}

.similTabla div {
    /* border: 2px solid black; */
    width: 35%;
    height: 2rem;
}

.input {
    /* width:23rem; */
    width: 90%;
    height: 100%;
    /* font-size: 0.95rem; */
    text-transform: capitalize;
    font-size: 1.2rem;


}


.inputGasto {
    margin-left: 0.5rem
}


.input:hover {

    border: 0.1rem solid gray;
    /*border-bottom: 0.5rem solid blue; */
    background: linear-gradient(to bottom, white 95%, blue 5%);
}


