.botonBootstrap {
    font-size: 2.5rem;

    text-decoration: none;
    width: 3rem;
    height: 3rem;
    color: black;
    box-shadow: none;
    margin: 0;
    padding: 0;

}


.botonBootstrap:hover {
    cursor: pointer;
    color: #00000090;

}



.container-input-icono {
    display: flex;
    justify-content: center;
    height: 1.5rem;
    align-items: center;
    margin: 0;
    padding: 0;
    width: 80rem;
    flex: 5;
    /*sirve como colspan*/
}

.container-input-icono:nth-child(3),
.container-input-icono:nth-child(1) {
    flex: 1;
}







