#eliminarFila::before {
    margin: 0;
    padding: 0;
    vertical-align: inherit;

}

.btnEliminar {
    height: 100%;
    width: 1rem
}

.aContenedorIcono {
    cursor: pointer;

}



/* :first-letter primer letra de elemento tengo que poner para mayusculas */



.divTransaccion {
    border-radius: 50px;

    margin: 0px auto;
    width: 60%;



    /*para alinear verticalmente*/
    height: 3em;
    display: flex;
    align-items: center;
    justify-content: center;

    background: linear-gradient(to right, #222FE6, #7881E6, #222FE6);
    border: 2px solid #1C26B8;
    border-radius: 1rem;
    text-align: center;
    box-shadow: 0px 5px 5px black;
    padding: 0.3em;
    overflow-wrap: break-word;
    margin-top: 2rem;
    margin-bottom: 2rem;
    /*
    #0014C4
    #242C78
    #0019F7
    #4B5CF9
    #000D7A
        */
    height: auto;
    overflow-wrap: break-word;

}



/* ver fondo de IMAGEN*/


.divTransaccion p {
    color: white;
    line-height: 2em;
    margin: initial;
}



.input-nombreJuntada {
    width: 20rem;
    font-weight: bold;
    border: none;
    background-color: transparent;
    border-bottom: 1px solid blue;
    /* background: linear-gradient(to top, blue,transparent,transparent); */
    background: linear-gradient(360deg, rgba(0, 109, 246, 1) 0%, rgba(30, 120, 236, 1) 1%, rgba(98, 144, 214, 1) 1.5%, transparent);
    margin-top: 1rem;

}









.divCEntradisimo {
    display: flex;
    justify-content: center;
    align-items: center
}


.btn4c {
    text-align: center;
    margin: auto;
    /* background:transparent; border:none;background: radial-gradient(circle at center,   blue, #0000FF88, transparent   );
    color:white;
    font-size: 1.5rem; */
}

.btn4c:hover {
    background: radial-gradient(circle at center, blue, #0000FF88, transparent);
    color: white;
}





.aContenedorIcono img:hover {
    background: radial-gradient(circle at center, blue, #0000FF88, transparent);

}



