.aUrlSheets {
    color: blue;

    margin-top: 1rem;
    cursor: pointer;


}




.inputsSheets {
    width: 40%;
}



.imgReverse {
    transform: scaleY(-1);
}


.imageButton {
    cursor: pointer;

}

.imageButton:hover {

    filter: opacity(0.4) drop-shadow(0 0 0 blue);

}


.containerGSheets {
    background-color: #0000ff10;
    padding: 0.5rem;
    border-radius: 2rem;
    background: linear-gradient(to top, #0000ff10, #0000ff10, #0000ff10, transparent);
}



.buscarGSheet {
    margin: 0;
}









.divFactura {
    background-color: white;

    /*para que tenga tamaño de a4*/
    width: 793.70px;
    height: 1122.52px;
    background-color: white;
    padding: 20px;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.1rem;

    position: relative;
    text-align: center
}













.imgIconoDesc {
    margin-bottom: 40px;
    display: inline-block;
}




.h3SubtDesc {
    margin-bottom: 20px;

}



.btnReset {
    margin-top: 1.5rem;
    margin-bottom: 0rem;
}






.lastRowReport {
    border-top: 1px solid black;
}

.lastRowReport td {
    padding: 0.3rem;
}


.tablaReporte {
    width: 100%;
    text-align: center;
    margin-top: 20px;
    font-size: 1rem
}

.headerRowReport {
    border-bottom: 2px solid black;

}




.divAllSpentsReport {

    position: absolute;
    bottom: 80px;
    left: 0;
    right: 0;
    color: #00000099;
    font-size: 0.7em;
    padding: 0 2rem
}








.footerReporte {
    position: absolute;
    text-align: center;
    bottom: 20px;
    left: 0;
    right: 0;
}



.colorBlue {
    color: blue
}


.dy-nn {
    display: none
}


@media screen and (max-width : 660px) {
    .divPersonaBorde {
        display: block;
        height: auto;
        border: 0.1px solid blue;
        border-radius: 0.5rem;

        background: radial-gradient(circle at center, transparent, transparent, transparent, #0000FF88);
    }

    .input {
        margin: 0;
        height: 1.5rem;
        font-size: medium;
        margin-top: 0.2rem;
    }

    .input {
        height: 1.5rem;
        font-size: medium;

    }

    .inputGasto {
        margin-top: 0.2rem;
        margin-bottom: 0.18rem;
    }




    .similTabla,
    .similTabla div {
        height: auto;
        margin-top: 1em !important;
    }


    .container-input-icono {
        height: auto;
        margin: 0rem;
    }

    .input-nombreJuntada {
        width: 82%;

    }

    .container-input-icono:nth-child(3) {

        margin-left: 0.5rem;
    }

    .aContenedorIcono img:hover {
        background: none;
    }

    .btn4c:hover {
        background: #eee;
        color: #444
    }



    .input:hover {

        border: 0.1rem solid gray;

        background: white;
    }

    .divTransaccion {
        width: 100%
    }

    body {
        padding: 2rem
    }



    .inputsSheets {
        width: 45%;
    }
}